@import '../../../../../library/src/sass/mixin';

.nav {
  background-color: transparent;
  -webkit-box-shadow: none;
          box-shadow: none;
  /**/
  overflow: hidden;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 10;

  &Sticky {
    background-color: #fff;
    -webkit-box-shadow: 0 5px 20px -10px #000;
            box-shadow: 0 5px 20px -10px #000;
  }

  &Bar {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    min-height: 40px;
    padding: 10px 15px 10px 50px;
  }
}

@media (max-width: 460px) {

  .nav {

    &Bar {
      padding-left: 15px;
    }
  }
}

.nav {

  &Bar {

    &Logo {
      opacity: 0;
      /**/
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;

      &Sticky {
        opacity: 1;
      }
    }

    &List {
      /**/
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      margin: 0;
    }
  }
}

@media (max-width: 460px) {

  .nav {

    &Bar {

      &List {
        display: none;
      }
    }
  }
}

.nav {

  &Bar {

    &List {

      &Toggle {
        display: block;
      }

      &Item {
        margin: 0 10px;
      }
    }
  }
}

@media (max-width: 460px) {

  .nav {

    &Bar {

      &List {

        &Item {
          margin-top: 12px;
          margin-bottom: 20px;
        }
      }
    }
  }
}

.nav {

  &Bar {

    &List {

      &Item {

        a:hover {
          color: #bc67ff;
        }

        &Link {
          text-transform: uppercase;
          letter-spacing: 3.5px;
          font-weight: 200;
          color: #fff;
        }

        &Link:visited {
          color: #fff;
        }

        &Link {

          &Sticky {
            color: #333;
          }

          &Sticky:visited {
            color: #333;
          }
        }
      }
    }

    &MobileToggle {
      display: none;
      -ms-flex-item-align: start;
          align-self: flex-start;
    }
  }
}

@media (max-width: 460px) {

  .nav {

    &Bar {

      &MobileToggle {
        display: block;
      }
    }
  }
}

.nav {

  &Bar {

    &MobileToggle {

      &Button {
        @include buttonModuleBase;

        path,
        svg {
          fill: #823eb7;
          stroke: #823eb7;
        }
      }

      &Button:hover:enabled {
        color: #fff;
        background-color: #823eb7;
        border-color: #823eb7;
      }

      &Button:hover:enabled path {
        fill: #fff;
        stroke: #fff;
      }

      &Button:hover:enabled svg {
        fill: #fff;
        stroke: #fff;
      }
    }
  }
}
