.container {
  color: rgba(0, 0, 0, .87);
  /* background-color: #ffffff;*/
  -webkit-transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
  transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  /* font-family: Roboto, sans-serif;*/
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  /* box-shadow: 0 1px 6px rgba(0, 0, 0, 0.12), 0 1px 4px rgba(0, 0, 0, 0.12);*/
  border-radius: 0;
  position: relative;
  z-index: 1100;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-left: 24px;
  padding-right: 24px;

  &Logo {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    margin: 0;
    padding-top: 0;
    letter-spacing: 0;
    font-size: 24px;
    font-weight: 400;
    color: #000;
    height: 64px;
    line-height: 64px;
    /* box-flex: 1;*/
    -webkit-box-flex: 1;
    -ms-flex: 1;
        flex: 1;
    cursor: pointer;

    &Image {
      margin-top: 4px;
    }
  }

  &SessionLinks {
    margin-top: 8px;
    margin-right: -16px;
    margin-left: auto;

    &Menu {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;

      &Item {
        border: 10px;
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
        display: block;
        /* font-family: Roboto, sans-serif;*/
        -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
        cursor: pointer;
        text-decoration: none;
        margin: 0;
        padding: 0;
        outline: none;
        font-size: 16px;
        font-weight: inherit;
        position: relative;
        color: rgba(0, 0, 0, .87);
        line-height: 48px;
        -webkit-transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
        transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
        min-height: 48px;
        white-space: nowrap;
        background: none;

        &Text {
          margin-left: 0;
          padding: 0 16px;
          position: relative;
        }
      }

      &Select {
        display: inline-block;
        position: relative;

        &Button {
          border: 10px;
          -webkit-box-sizing: border-box;
                  box-sizing: border-box;
          display: inline-block;
          /* font-family: Roboto, sans-serif;*/
          -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
          cursor: pointer;
          text-decoration: none;
          margin: 0;
          padding: 12px;
          outline: none;
          font-size: 0;
          font-weight: inherit;
          position: relative;
          overflow: visible;
          -webkit-transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
          transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
          width: 48px;
          height: 48px;
          background: none;

          &Image {
            display: inline-block;
            color: rgba(0, 0, 0, .87);
            fill: currentcolor;
            height: 24px;
            width: 24px;
            -webkit-user-select: none;
               -moz-user-select: none;
                -ms-user-select: none;
                    user-select: none;
            -webkit-transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
            transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
          }
        }
      }
    }
  }
}
