@import '../../../../../library/src/sass/mixin';
@import './Book.part';

.topBorder {
  border-top: 1px solid #e4e4e4;
}

.container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  text-align: center;
  padding: 85px 20px;
  max-width: 800px;
  margin: 0 auto;
}

@media (max-width: 720px) {

  .container {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
  }
}

.container {

  &Image {
    position: relative;
    overflow: hidden;
    /**/
    min-width: 250px;
    max-width: 300px;
    margin: 20px;

    @include pictureImage();
  }

  &Info {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;

    &Lead {
      font-weight: 300;
      font-size: 21px;
      margin: 10px 0;
    }

    &Link:visited {
      color: #fff;
      background-color: #823eb7;
    }

    &Link {
      /*primary button*/
      display: inline-block;
      color: #fff;
      background-color: #823eb7;
      margin: 0;

      @include buttonModuleBase;

      @include buttonModuleBaseLarge;

      @include buttonModuleBaseRound;
    }

    &Link:visited path,
      &Link:visited svg {
      fill: #fff;
      stroke: #fff;
    }
  }
}
